;IACzB,YAAY;IACZ,aAAa;IACb,eAAe;IACf,YAAY;AAChB;AAEA;IACI,kBAAkB;IAClB,qBAAqB;AACzB;AAEA;IACI,cAAc;IACd,kBAAkB;IAClB,yBAAyB;IACzB,eAAe;IACf,gBAAgB;IAChB,+CAA+C;IAC/C,UAAU;IACV,UAAU;IACV,iBAAiB;AACrB;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,qBAAqB;IACrB,cAAc;AAClB;AAEA;IACI,yBAAyB;AAC7B;AAEA;IACI,kBAAkB;AACtB;AACA;IACI,6BAA6B;IAC7B,iBAAiB;IACjB,oBAAoB;IACpB,mBAAmB;IACnB,gBAAgB;AACpB;AAEA;IACI,kBAAkB;IAClB,4KAA4K;IAC5K,qDAAqD;IACrD,uBAAuB;IACvB,2CAA2C;IAC3C,sBAAsB;IACtB,cAAc;IACd,yBAAyB;IACzB,UAAU;AACd","sourcesContent":["<template>\n\n        <div @click=\"getData\"><i class=\"mdi mdi-refresh\"></i> Refresh</div>\n    <!-- <diV v-if=\"artifacts.length > 0\">\n        <div v-for=\"artifact in artifacts\" style=\"display:flex\" @click=\"downloadArtifact(artifact)\"\n            :title=\"`Download: ${artifact.name}`\">\n            <i class=\"mdi mdi-monitor artifactIcons\"></i> {{ artifact.type }}\n        </div>\n    </diV> -->\n    <div v-for=\"step in steps\" :key=\"step.ID\" v-bind:value=\"step.ID\">\n        <div data-bs-toggle=\"collapse\" role=\"button\" aria-expanded=\"false\" aria-controls=\"multiCollapseExample2131\">\n            <div class='expandContent' @click=\"chooseStep(step.ID, step.name)\">\n                <i   style='font-size: 16px;' :class=\"step.isOpen ? 'mdi mdi-chevron-down' : 'mdi mdi-chevron-right'\"></i>\n                <i :style=\"`color:${getStatusIconColor(step.status)}; font-size: 16px; padding-left: 15px;`\"\n                    :class='getStatusIconClass(step.status)'></i>{{ step.name }}\n            </div>\n\n            \n            <div class=\"logContent\" v-if=\"step.isOpen\">\n                <pre>\n                    <div v-if=\"props.type == 'deploy' && step.name == 'Pipeline Started'\">\n                        <div>Your artifact has been putted in the repository, and your deployment process is progressing.</div>\n                        <div>You can follow the next steps on Jenkins by clicking the link below.</div>\n                        <div class=\"outsideUrl\" @click=\"openPipelineJobUrl()\">GO TO LINK</div>\n                    </div>\n                    <div v-if=\"props.type == 'deploy' && step.name == 'Deployment Completed' && props.job.jobData.type == 'module'\">\n                        <div class=\"outsideUrl\" @click=\"openEnvironmentUrl('serviceUrl')\">OPEN SERVICE SWAGGER URL</div>\n                        <div class=\"outsideUrl\" @click=\"openEnvironmentUrl('serviceUIUrl')\">OPEN UI URL</div>\n                    </div>\n                    <div v-if=\"props.type == 'deploy' && step.name == 'Deployment Completed' && props.job.jobData.type == 'application'\">\n                        <div class=\"outsideUrl\" @click=\"openEnvironmentUrl('quiURL')\">OPEN QUI</div>\n                    </div>\n                    <div v-if=\"props.type != 'deploy' || (step.name != 'Pipeline Started' && step.name != 'Deployment Completed')\" v-for=\"log in step.logs\">{{ log }}</div>\n                </pre>\n            </div>\n        </div>\n    </div>\n    <div v-if=\"props.type == 'export'\" class=\"infoText\"  style=\"display:flex\">\n        <div class='col-10 ' style=\"text-align:end\">\n                  <div style=\"padding-top: 8px;\">Download The Artifacts of Job</div>\n        </div>\n        <div class=\"dropdown col-2\" style=\"text-align:end\">\n  \n            <button @click=\"artifactList\" class=\" artifactDownloadBtn\"><i class='mdi mdi-tray-arrow-down'\n                    style=\"padding-right: 8px;\"></i>DOWNLOAD <i class='mdi mdi-chevron-down'></i></button>\n            <div v-if=\"artifactListVisible\" v-for=\"artifact in artifacts\" class=\"dropdown-content\">\n                <span @click=\"downloadArtifact(artifact)\"  class='artifactListItem'><i :class=\"computedArtifact(artifact.type)\"></i>{{ artifact.type\n                }}</span>\n            </div>\n        </div>\n    </div>\n\n\n</template>\n\n<script setup lang=\"ts\">import { container } from 'tsyringe';\nimport { onMounted, onUnmounted, ref } from 'vue';\nimport 'bootstrap/js/dist/collapse'\nimport { IDeployJobData, IGitlabEnvironment, JobType   } from \"@stechquick/algae/lib/qCloudTemp/exporter\";\nimport { IExportJobArtifactInfoItem, IExportJobItem, IExportJobStepItem } from '../../../../domain/model/models';\nimport { IListExportJobStepLogs } from '../../../../domain/useCase/IListExportJobStepLogs';\nimport { IListExportJobSteps } from '../../../../domain/useCase/IListExportJobSteps';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport { IViewModelManager } from '../../../../domain/viewModel/IViewModelManager';\nimport { IListExportJobArtifacts } from '../../../../domain/useCase/IListExportJobArtifacts';\nimport { IConfig } from '../../../../domain/infrastructure/IConfig';\nimport { INetwork } from '../../../../domain/infrastructure/INetwork';\nimport { IViewModel } from '../../../../domain/viewModel/IViewModel';\nimport { createError } from '../../../../domain/model/shellError';\nimport { StatusType } from '@stechquick/algae/lib/qCloudTemp/exporter';\nimport { IDeployJobs } from '../../../../domain/useCase/IDeployJobs';\nimport { JobUtilsImpl } from '../../../../application/useCases/jobUtilsImpl';\ninterface IExtentedStepItem extends IExportJobStepItem {\n    isOpen?: boolean\n}\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst config = container.resolve<IConfig>(IConfig);\nconst network = container.resolve<INetwork>(INetwork);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst props = defineProps<{ job: IExportJobItem, type:JobType}>();\nconst viewModelManager = container.resolve<IViewModelManager>(IViewModelManager);\nconst deployJobsUseCase = container.resolve<IDeployJobs>(IDeployJobs);\nconst steps = ref<Array<IExtentedStepItem>>([]);\nconst artifactListVisible =ref(false)\nconst artifacts = ref<Array<IExportJobArtifactInfoItem>>([]);\nconst pipelineBaseUrl = config.getValue(\"rallyJenkinsUrl\");\nconst frontendHandledSteps = [\"Pipeline Started\", \"Deployment Completed\"];\n\nconst serviceResultBucket: {\n    serviceRepoNameVal: string | undefined,\n    productRepoNameVal: string | undefined,\n    productConfigInfoVal: { domain: string, quiName?: string } | undefined\n} = { serviceRepoNameVal: undefined, productRepoNameVal: undefined, productConfigInfoVal: undefined };\n\n\n\nfunction getData() {\n    executor.execute(async () => {\n        await executor.executeUseCase<IListExportJobSteps>(IListExportJobSteps, { jobID: props.job.jobID });\n        const currentJob = viewModelManager.findObject(props.job.jobID) as IExportJobItem\n        steps.value = currentJob.steps!\n        if (props.job.status == 'success') {\n            await executor.executeUseCase<IListExportJobArtifacts>(IListExportJobArtifacts, { jobID: props.job.jobID });\n            artifacts.value = currentJob.artifacts!\n        }\n\n    }, { loading: true });\n}\ndefineExpose({\n  getData,\n});\n\ngetData();\n\n\nfunction getStatusIconColor(status: StatusType) {\n    switch (status) {\n        case \"success\": return \"#149911\";\n        case \"failed\": return \"red\";\n        case \"running\": return \"blue\";\n        case \"waiting\": return \"grey\";\n    }\n}\n\nfunction getStatusIconClass(status: StatusType) {\n    switch (status) {\n        case \"success\": return \"mdi mdi-check-circle-outline\";\n        case \"failed\": return \"mdi mdi-close-circle-outline\";\n        case \"running\": return \"mdi mdi-progress-upload\";\n        case \"waiting\": return \"mdi mdi-timer-sand\";\n    }\n}\n\n// async function setJobStatus(status: StatusType){\n\n// }\n\n// async function checkStatusForDeployJob() {\n//     const statusList = steps.value.map(i => i.status);\n\n//     const isAnyFailed = statusList.indexOf(\"failed\");\n//     if (isAnyFailed) {\n//         setJobStatus(\"failed\");\n//         return;\n//     }\n\n    \n// }\n\nfunction openUrl(url: string){\n    window.open(url, \"_blank\");\n}\n\nasync function getServiceRepoName() {\n    if(serviceResultBucket.serviceRepoNameVal){ return serviceResultBucket.serviceRepoNameVal };\n\n    const jobData = props.job.jobData as IDeployJobData;\n    const environment = jobData.environment as IGitlabEnvironment;\n    const curProjectRepoId = environment.params.repoId;\n    serviceResultBucket.serviceRepoNameVal = await deployJobsUseCase.getRepoNameById(curProjectRepoId);\n    \n    return serviceResultBucket.serviceRepoNameVal;\n}\n\nasync function getProductRepoName() {\n    if(serviceResultBucket.productRepoNameVal){ return serviceResultBucket.productRepoNameVal; }\n\n    const jobData = props.job.jobData as IDeployJobData;\n    const environment = jobData.environment as IGitlabEnvironment;\n    const productRepoId = environment.params.productRepoId!;\n    serviceResultBucket.productRepoNameVal = await deployJobsUseCase.getRepoNameById(productRepoId);\n\n    return serviceResultBucket.productRepoNameVal;\n}\n\nasync function getProductConfigInfo() {\n    const jobData = props.job.jobData as IDeployJobData;\n\n    if(jobData.type == \"application\" && serviceResultBucket.productConfigInfoVal?.quiName){ \n        return serviceResultBucket.productConfigInfoVal; \n    }\n    else if(serviceResultBucket.productConfigInfoVal){ \n        return serviceResultBucket.productConfigInfoVal; \n    }\n    const environment = jobData.environment as IGitlabEnvironment;\n    const productRepoId = environment.params.productRepoId!;\n\n    let getter = () => deployJobsUseCase.getProductConfigInfo(productRepoId);\n    if(jobData.type == \"application\"){\n        const quiRepoID = environment.params.repoId;\n        getter = () => deployJobsUseCase.getProductConfigInfo(productRepoId, quiRepoID);\n    }\n\n    serviceResultBucket.productConfigInfoVal = await getter();\n\n    return serviceResultBucket.productConfigInfoVal;\n}\n\nasync function openPipelineJobUrl() {\n    await executor.execute(async () => {\n        const jobData = props.job.jobData as IDeployJobData;\n        const environment = jobData.environment as IGitlabEnvironment;\n        const repoName = await deployJobsUseCase.getRepoNameById(environment.params.repoId);\n        const url = `${pipelineBaseUrl}job/mr-${repoName}`;\n        openUrl(url);\n    }, { loading: true });\n}\n\nasync function openEnvironmentUrl(type: \"serviceUrl\" | \"serviceUIUrl\" | \"quiURL\") {\n    await executor.execute(async () => {\n        const promiseResults = await Promise.all([getServiceRepoName(), getProductRepoName(), getProductConfigInfo()]);\n        const [serviceRepoName, productRepoName, productConfigInfo] = promiseResults;\n\n        const productName = productRepoName.replace(\"product-\", \"\");\n\n        switch (type) {\n            case \"serviceUrl\": openUrl(`http://${serviceRepoName}.${productName}-int.${productConfigInfo.domain}/swagger-ui.html`); break;\n            case \"serviceUIUrl\": openUrl(`http://${serviceRepoName}-ui.${productName}-int.${productConfigInfo.domain}/`); break;\n            case \"quiURL\": openUrl(`http://${productConfigInfo.quiName}.${productName}-int.${productConfigInfo.domain}/`); break;\n        }\n    }, { loading: true });\n}\n\nasync function chooseStep(stepID: string, stepName: string) {\n    const currentStep = steps.value.find(i => i.ID == stepID)!;\n    if (currentStep.isOpen) {\n        currentStep.isOpen = false;\n        return;\n    }\n    if(props.type == \"deploy\" && frontendHandledSteps.indexOf(stepName) > -1) {\n        currentStep.isOpen = true;\n        return;\n    }\n    executor.execute(async () => {\n        await executor.executeUseCase<IListExportJobStepLogs>(IListExportJobStepLogs, { stepID });\n        currentStep.isOpen = true;\n    }, { loading: true });\n}\nfunction computedArtifact(type: string) {\n    if (type == 'web') {\n        return 'mdi mdi-monitor artifactIcons'\n    }\n}\n\nfunction getArtifactLink(artifact: IExportJobArtifactInfoItem) {\n    const token = viewModel.user!.authorization;\n    const { objectName } = artifact.details;\n    const url = config.getValue(\"providerApiUrl\");\n    return network.join(url, `/minio/getExportJobArtifact?objectName=${objectName}&t=${token}`);\n}\n\nfunction artifactList(){\n        if(artifactListVisible.value){\n        artifactListVisible.value=false\n    }\n    else{\n        artifactListVisible.value=true\n    }\n}\nfunction downloadArtifact(artifact: IExportJobArtifactInfoItem) {\n\n    const a = document.createElement(\"a\");\n    a.href = getArtifactLink(artifact);\n    a.target = \"new\";\n    a.download = \"download\";\n    document.body.appendChild(a);\n    a.click();\n    document.body.removeChild(a);\n    console.log('dada')\n}\nconst messageHandler: (ev: MessageEvent) => void = (ev) => {\n    executor.execute(async () => {\n        let message: { downloaderTab: boolean, error: string, stack: string } | undefined = ev.data;\n        if (!message || !message.downloaderTab) { return; }\n\n        throw createError({ message: message.error, type: \"technical\" });\n    }, { loading: false });\n};\nonMounted(() => window.addEventListener(\"message\", messageHandler));\nonUnmounted(() => window.removeEventListener(\"message\", messageHandler));\n\n</script>\n\n\n<style scoped>\n.expandContent:hover {\n    background-color: #151233;\n    color: white\n}\n\n.artifactDownloadBtn {\n    background-color: #151233;\n    width: 138px;\n    height: 32px;\n    border-radius: 4px;\n    opacity: 1;\n    color: white;\n}\n\n.dropbtn {\n    background-color: #04AA6D;\n    color: white;\n    padding: 16px;\n    font-size: 16px;\n    border: none;\n}\n\n.dropdown {\n    position: relative;\n    display: inline-block;\n}\n\n.dropdown-content {\n    display: block;\n    position: absolute;\n    background-color: #f1f1f1;\n    text-align: end;\n    min-width: 140px;\n    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);\n    z-index: 1;\n    right: 0px;\n    text-align: start;\n}\n\n.dropdown-content span {\n    color: black;\n    padding: 12px 16px;\n    text-decoration: none;\n    display: block;\n}\n\n.dropdown-content span:hover {\n    background-color: #E3E3E3;\n}\n\n.artifactIcons {\n    padding-right: 8px;\n}\n.infoText{\n    border-top: 1px solid #E3E3E3;\n    padding-top: 20px;\n    padding-bottom: 20px;\n    padding-right: 20px;\n    margin-top: 10px;\n}\n\n.outsideUrl{\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-medium) var(--unnamed-font-size-14)/var(--unnamed-line-spacing-19) var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0-02);\n    /* text-align: right; */\n    font: normal normal medium 14px/19px Roboto;\n    letter-spacing: 0.02px;\n    color: #151233;\n    text-transform: uppercase;\n    opacity: 1;\n}\n</style>"],"sourceRoot":""}]);
