function openUrl(url: string){
    window.open(url, "_blank");
}

async function getServiceRepoName() {
    if(serviceResultBucket.serviceRepoNameVal){ return serviceResultBucket.serviceRepoNameVal };

    const jobData = props.job.jobData as IDeployJobData;
    const environment = jobData.environment as IGitlabEnvironment;
    const curProjectRepoId = environment.params.repoId;
    serviceResultBucket.serviceRepoNameVal = await deployJobsUseCase.getRepoNameById(curProjectRepoId);
    
    return serviceResultBucket.serviceRepoNameVal;
}

async function getProductRepoName() {
    if(serviceResultBucket.productRepoNameVal){ return serviceResultBucket.productRepoNameVal; }

    const jobData = props.job.jobData as IDeployJobData;
    const environment = jobData.environment as IGitlabEnvironment;
    const productRepoId = environment.params.productRepoId!;
    serviceResultBucket.productRepoNameVal = await deployJobsUseCase.getRepoNameById(productRepoId);

    return serviceResultBucket.productRepoNameVal;
}

async function getProductConfigInfo() {
    const jobData = props.job.jobData as IDeployJobData;

    if(jobData.type == "application" && serviceResultBucket.productConfigInfoVal?.quiName){ 
        return serviceResultBucket.productConfigInfoVal; 
    }
    else if(serviceResultBucket.productConfigInfoVal){ 
        return serviceResultBucket.productConfigInfoVal; 
    }
    const environment = jobData.environment as IGitlabEnvironment;
    const productRepoId = environment.params.productRepoId!;

    let getter = () => deployJobsUseCase.getProductConfigInfo(productRepoId);
    if(jobData.type == "application"){
        const quiRepoID = environment.params.repoId;
        getter = () => deployJobsUseCase.getProductConfigInfo(productRepoId, quiRepoID);
    }

    serviceResultBucket.productConfigInfoVal = await getter();

    return serviceResultBucket.productConfigInfoVal;
}

async function openPipelineJobUrl() {
    await executor.execute(async () => {
        const jobData = props.job.jobData as IDeployJobData;
        const environment = jobData.environment as IGitlabEnvironment;
        const repoName = await deployJobsUseCase.getRepoNameById(environment.params.repoId);
        const url = `${pipelineBaseUrl}job/mr-${repoName}`;
        openUrl(url);
    }, { loading: true });
}

async function openEnvironmentUrl(type: "serviceUrl" | "serviceUIUrl" | "quiURL") {
    await executor.execute(async () => {
        const promiseResults = await Promise.all([getServiceRepoName(), getProductRepoName(), getProductConfigInfo()]);
        const [serviceRepoName, productRepoName, productConfigInfo] = promiseResults;

        const productName = productRepoName.replace("product-", "");

        switch (type) {
            case "serviceUrl": openUrl(`http://${serviceRepoName}.${productName}-int.${productConfigInfo.domain}/swagger-ui.html`); break;
            case "serviceUIUrl": openUrl(`http://${serviceRepoName}-ui.${productName}-int.${productConfigInfo.domain}/`); break;
            case "quiURL": openUrl(`http://${productConfigInfo.quiName}.${productName}-int.${productConfigInfo.domain}/`); break;
        }
    }, { loading: true });
}

async function chooseStep(stepID: string, stepName: string) {
    const currentStep = steps.value.find(i => i.ID == stepID)!;
    if (currentStep.isOpen) {
        currentStep.isOpen = false;
        return;
    }
    if(props.type == "deploy" && frontendHandledSteps.indexOf(stepName) > -1) {
        currentStep.isOpen = true;
        return;
    }
    executor.execute(async () => {
        await executor.executeUseCase<IListExportJobStepLogs>(IListExportJobStepLogs, { stepID });
        currentStep.isOpen = true;
    }, { loading: true });
}
function computedArtifact(type: string) {
    if (type == 'web') {
        return 'mdi mdi-monitor artifactIcons'
    }
}

function getArtifactLink(artifact: IExportJobArtifactInfoItem) {
    const token = viewModel.user!.authorization;
    const { objectName } = artifact.details;
    const url = config.getValue("providerApiUrl");
    return network.join(url, `/minio/getExportJobArtifact?objectName=${objectName}&t=${token}`);
}

function artifactList(){
        if(artifactListVisible.value){
        artifactListVisible.value=false
    }
    else{
        artifactListVisible.value=true
    }
}
function downloadArtifact(artifact: IExportJobArtifactInfoItem) {

    const a = document.createElement("a");
    a.href = getArtifactLink(artifact);
    a.target = "new";
    a.download = "download";
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    console.log('dada')
}
