    executor.execute(async () => {
        let message: { downloaderTab: boolean, error: string, stack: string } | undefined = ev.data;
        if (!message || !message.downloaderTab) { return; }

        throw createError({ message: message.error, type: "technical" });
    }, { loading: false });
};
onMounted(() => window.addEventListener("message", messageHandler));
onUnmounted(() => window.removeEventListener("message", messageHandler));


